import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface DividerProps extends NamespaceWrapperProps {
    className ?: string;

    size ?: "large";

    spacing ?: "compact" | "spacious";
}

export default class Divider extends NamespaceWrapper<DividerProps, {}> {
    public static displayName = "Divider";

    public static propTypes = {//
        "className": PropTypes.string, //
        "size": PropTypes.oneOf(["large"]), //
        "spacing": PropTypes.oneOf(["compact", "spacious"])//
    };

    public render() {
        const className = this.classes(
            "Divider",
            this.props.size && "Divider--" + this.props.size,
            this.props.spacing && "Divider--" + this.props.spacing,
            this.props.className
        );

        return <hr className={className}>{this.props.children}</hr>;
    }
}
